import React from 'react';
import '../App.css';

function getListCount(set,o){
    let listS = o.SHEET_WIDTH*o.SHEET_LENGHT*o.SHEET_FILLING_AREA;
    let S = 0;
    let listCount = set.size > 0 ? 1 : 0;
    for (let detail of set.values()) {
        let width = detail.width;
        let height = detail.height;
        S += width*height; // Считаем площадь детали по чистым размерам
        S > listS*listCount && listCount++;
    }
    return listCount;
}

function Summary(props){

    let set = props.set;
    let o = props.options;
    let summaryOut = [];
    let summary = {
        'Материал': o.SHEET_NAME,
        'Размер': `${o.SHEET_WIDTH}мм x ${o.SHEET_LENGHT}мм x ${o.SHEET_DEPTH}мм`,
        'Листов в раскрое': getListCount(set,o),
        'Цена за лист': `${o.SHEET_PRICE} руб.`,
        'Всего деталей': set.size,
    }

    for(let key in summary){
        summaryOut.push(<SummItem key={key} title={key} value={summary[key]} />);
    }
    
    return(
        <div id="summary">
            {summaryOut}
            <div className="summ-item">
                <span><b>Примерная стоимость:</b></span>
                <span><b>{summ(set,o,true)}</b></span>
            </div>
            <MoreOrderDetails set={set} o={o} />
        </div>
    )
}

export {Summary};

class MoreOrderDetails extends React.Component{

    state = {
        open: false,
    }

    render(){
        if(this.state.open)
            return(
                <div className="summ-item more">
                    <span onClick={() => this.setState({open:false})} className="action-link red w100p"><b>Свернуть</b></span>
                    {summ(this.props.set, this.props.o)}
                </div>
            );
        else
            return(
                <div className="summ-item">
                    <span onClick={() => this.setState({open:true})} className="action-link red"><b>Подробнее о расчёте</b></span>
                </div>
            );
    }
}

function SummItem(props){

    return(
        <div className="summ-item">
            <span>{props.title}: </span>
            <span>{props.value}</span>
        </div>
    )
}

function summ(set,o,final=false){
    let listS = o.SHEET_WIDTH*o.SHEET_LENGHT*o.SHEET_FILLING_AREA;
    let S = 0;
    let listCount = set.size > 0 ? 1 : 0;
    let edge = {sm:0, lg:0}
    let angle = 0;
    let groove = 0;
    let loops = 0;
    for (let detail of set.values()) {
        let width = detail.width;
        let height = detail.height;
        S += width*height; // Считаем площадь детали по чистым размерам
        S > listS*listCount && listCount++; // Если не хватает площади листа, добавляем лист
        for(let edgeSide in detail.edge){ // Кромление
            let edgeSize = detail.edge[edgeSide];
            if( edgeSize > 0 && edgeSize < 1){
                edgeSide === 'left' || edgeSide === 'right' ? edge.sm += height : edge.sm += width;
            }
            else if(edgeSize > 0 && edgeSize > 2){
                edgeSide === 'left' || edgeSide === 'right' ? edge.lg += height : edge.lg += width;
            }
        }
        // Углы
        if(detail.lt.x > 0 || detail.rt.x > 0 || detail.rb.x > 0 || detail.lb.x > 0)
            angle++;
        // Петли
        if(detail.loops && detail.loops.side !== 'none')
            loops += detail.loops.count;
        // Пазы
        if(detail.groove)
            groove++;
        
    }
    let edgeSM = edge.sm*o.EDGE_MULTIPLICATOR/1000*Math.abs(o.PRICE_EDGE_04);
    let edgeLG = edge.lg*o.EDGE_MULTIPLICATOR/1000*Math.abs(o.PRICE_EDGE_02);
    let edgeSumm = Math.round(edgeSM+edgeLG+((edge.sm+edge.lg)*o.EDGE_MULTIPLICATOR/1000*Math.abs(o.PRICE_EDGING)));
    
    let material = listCount*o.SHEET_PRICE;
    let work = listCount*o.PRICE_SHEET_CUTTING;
    let angles = angle*Math.abs(o.PRICE_CURVATURE);
    let finalLoops = loops*Math.abs(o.PRICE_LOOP);
    let finalGrooves = groove*Math.abs(o.PRICE_GROOVE);
    let pack = listCount> 0 ? 200 : 0;

    if(final){
        let finalSumm = material+work+edgeSumm+angles+finalLoops+finalGrooves+pack;
        return finalSumm + ' руб.';
    }
    return(
        <table className="w100p">
            <tbody>
                <tr><td>Материал</td><td>{material}</td></tr>
                <tr><td>Рез</td><td>{work}</td></tr>
                <tr><td>Кромление</td><td>{edgeSumm}</td></tr>
                <tr><td>Криволинейность</td><td>{angles}</td></tr>
                <tr><td>Петли</td><td>{finalLoops}</td></tr>
                <tr><td>Пазы</td><td>{finalGrooves}</td></tr>
                <tr><td>Упаковка</td><td>{pack}</td></tr>
            </tbody>
        </table>
    );
}