import React from 'react';
import '../App.css';

// ============================================ GROOVE
class GrooveOptions extends React.Component{

    handleInputChange = (e) => {
        let fieldName = e.target.name; // Поле формы
        let value = e.target.value; // Значение формы
        let key = e.target.closest('.detail').dataset.key; // Ключ детали

        this.setState({
            [fieldName]: value,
            error: false
          }, () => {
            this.props.func(this.state.side, key); // отправлем данные в Арр
          }
        )
    }

    isFree = (r,A1,A2) => {
        if(r === 'loops' || A1 || A2){
            return true;
        }
        if(!r) return false;
    }

    render(){
        let set = this.props.set;
        let LTangle = +set.lt.x === 0 ? false : true;
        let RTangle = +set.rt.x === 0 ? false : true;
        let RBangle = +set.rb.x === 0 ? false : true;
        let LBangle = +set.lb.x === 0 ? false : true;
        let r = set.reserve;
        let resL = this.isFree(r.left,LTangle,LBangle);
        let resT = this.isFree(r.top,LTangle,RTangle);
        let resR = this.isFree(r.right,RTangle,RBangle);
        let resB = this.isFree(r.bot,RBangle,LBangle);

        if(r.left==='loops' || r.top==='loops' || r.right==='loops' || r.bot==='loops'){
            return(
                <span className="fons12 red">Пазы несовместимы с петлями</span>
            )
        }

        if(resL && resT && resR && resB){
            return(
                <span className="fons12 red">Нет вариантов</span>
            )
        }

        return(
            <div className="flex-nowrap flex-center">
                <label className="flex-column">
                    <span>Сторона</span>
                    <select onChange={this.handleInputChange} name="side" value={set.groove}>
                        <option value="none">Нет</option>
                        <option value="left"  disabled={resL}>Слева{resL && ' (занято)'}</option>
                        <option value="top"   disabled={resT}>Сверху{resT && ' (занято)'}</option>
                        <option value="right" disabled={resR}>Справа{resR && ' (занято)'}</option>
                        <option value="bot"   disabled={resB}>Снизу{resB && ' (занято)'}</option>
                    </select>
                </label>
                <span className="fons12">
                    <b>Параметры паза: </b>
                    глубина 8 мм,
                    ширина 4 мм,
                    расстояние от края 16 мм (20 мм вместе с пазом)
                </span>
            </div>
        );
    }
}

export {GrooveOptions};

function Groove(props){
    let set = props.set;
    let m = props.m;
    let side = set.groove;
    
    if(!side) return('');
    
    let indent = set.grooveIndent;
    let width = set.grooveWidth;
    let x = m;
    let y = m;
    let vector = side === 'left' || side === 'right' ? 'v' : 'h';
    let size = vector === 'h' ? set.width : set.height;
    
    if(side === 'left')
        x = m + indent; size = size*-1;
    if(side === 'top')
        y = m + indent;
    if(side === 'right')
        x = m + +set.width - indent;
    if(side === 'bot')
        y = +set.height + m - indent; size = size*-1;

    return <path d={`M ${x} ${y} ${vector} ${size}`} stroke="green" strokeWidth={width} fill="transparent"/>
}
export {Groove};