import React from 'react';
import '../App.css';

// ============================================ GROOVE
class EdgeOptions extends React.Component{

    state = {
        left: '0.0', top: '0.0', right: '0.0', bot: '0.0',
    }

    handleChange = (e) => {
        let set   = this.props.set;
        let lta = set.lt.x > 0 ? true : false;
        let rta = set.rt.x > 0 ? true : false;
        let rba = set.rb.x > 0 ? true : false;
        let lba = set.lb.x > 0 ? true : false;

        let fieldName = e.target.name; // Поле формы
        let value = e.target.value; // Значение формы
        let key = e.target.closest('.detail').dataset.key; // Ключ детали
        let newValue = {[fieldName]: value}

        if(fieldName === 'left' && lta)
            newValue.top = value;
        else if(fieldName === 'left' && lba)
            newValue.bot = value;
        else if(fieldName === 'top' && lta)
            newValue.left = value;
        else if(fieldName === 'top' && rta)
            newValue.right = value;
        else if(fieldName === 'right' && rta)
            newValue.top = value;
        else if(fieldName === 'right' && rba)
            newValue.bot = value;
        else if(fieldName === 'bot' && rba)
            newValue.right = value;
        else if(fieldName === 'bot' && lba)
            newValue.left = value;

        this.setState( newValue, () => {
            this.props.func(this.state, key); // отправлем данные в Арр
          }
        )
    }

    render(){
        let set   = this.props.set;
        let left  = set.edge.left;
        let top   = set.edge.top;
        let right = set.edge.right;
        let bot   = set.edge.bot;
        let min   = set.edgeMin;
        let max = set.edgeMax;
        return(
            <>
            <label className="flex-column">
                <span>Слева</span>
                <select onChange={this.handleChange} name="left" value={left}>
                    <option value="0.0">0.0</option>
                    <option value={min}>{min}</option>
                    <option value={max}>{max}</option>
                </select>
            </label>
            <label className="flex-column">
                <span>Сверху</span>
                <select onChange={this.handleChange} name="top" value={top}>
                    <option value="0.0">0.0</option>
                    <option value={min}>{min}</option>
                    <option value={max}>{max}</option>
                </select>
            </label>
            <label className="flex-column">
                <span>Справа</span>
                <select onChange={this.handleChange} name="right" value={right}>
                    <option value="0.0">0.0</option>
                    <option value={min}>{min}</option>
                    <option value={max}>{max}</option>
                </select>
            </label>
            <label className="flex-column">
                <span>Снизу</span>
                <select onChange={this.handleChange} name="bot" value={bot}>
                    <option value="0.0">0.0</option>
                    <option value={min}>{min}</option>
                    <option value={max}>{max}</option>
                </select>
            </label>
            </>
        );
    }
}

export {EdgeOptions};

function Edge(props){
    let {set,m} = props;
    let length = 100;
    let lx = m - 10;
    let ly = m + set.height + 10;
    let rx = m + set.width + 10;
    return(
        <>
        <path d={`M ${lx} ${ly} v -${length}`} stroke="brown" strokeDasharray="15,10" strokeWidth="3" fill="transparent" />
        <text fontSize="150%" x={lx-45} y={ly-25} fill="brown">{set.edge.left}</text>
        <path d={`M ${lx} ${ly} h ${length}`} stroke="brown" strokeDasharray="15,10" strokeWidth="3" fill="transparent" />
        <text fontSize="150%" x={lx+30} y={ly+25} fill="brown">{set.edge.bot}</text>

        <path d={`M ${rx} ${lx} v ${length}`} stroke="brown" strokeDasharray="15,10" strokeWidth="3" fill="transparent" />
        <text fontSize="150%" x={rx-60} y={lx-10} fill="brown">{set.edge.top}</text>
        <path d={`M ${rx} ${lx} h -${length}`} stroke="brown" strokeDasharray="15,10" strokeWidth="3" fill="transparent" />
        <text fontSize="150%" x={rx+5} y={lx+50} fill="brown">{set.edge.right}</text>
        </>
    );
}
export {Edge};