import React from 'react';
import '../App.css';

// ============================================ LOOPS
class LoopsOptions extends React.Component{
    state = {
      side: 'none',
      count: 2,
      before: 100,
      after: 500,
      minSpace: 50,
      error: false,
    }
  
    handleInputChange = (e) => {
      let fieldName = e.target.name; // Поле формы
      let value = e.target.value; // Значение формы
      let key = e.target.closest('.detail').dataset.key; // Ключ детали
      let steps = this.state.count - 1; // желаемое кол-во отрезков между первой и последней деталью
      let step = Math.round((this.state.after-this.state.before)/steps, 1); // размер одного отрезка
    //  Не позволяем добавлять больше петель, 
    //  чем это возможно при сохранении минимального расстояния между центрами петель,
    //  обозначенного как minSpace, не путать с indent(отступ петли от края детали)
      if(step >= this.state.minSpace){
        this.setState({
          [fieldName]: value,
          error: false
        }, () => {
          this.props.func(this.state, key); // отправлем данные в Арр
        })
        
      }
      else{
        this.setState({
          [fieldName]: value,
          error: true
        });
        
      }
    }

    isFree = (r,A1,A2) => {
        if(r === 'groove' || A1 || A2){
            return true;
        }
        if(!r) return false;
    }
  
    render(){
        let {side,before,after,minSpace} = this.state;
        let set = this.props.set;
        let beforeMin = +minSpace*2;
        let beforeMax = after - minSpace*2;
        let afterMax = side === 'top' || side === 'bot' ? set.nominalwidth - minSpace*2 : set.nominalheight - minSpace*2;
        let afterMin = +minSpace*2 + +before;
        let countMax = Math.round((after-before)/minSpace) + 1;
        let LTangle = +set.lt.x === 0 ? false : true;
        let RTangle = +set.rt.x === 0 ? false : true;
        let RBangle = +set.rb.x === 0 ? false : true;
        let LBangle = +set.lb.x === 0 ? false : true;
        let r = set.reserve;
        let resL = this.isFree(r.left,LTangle,LBangle);
        let resT = this.isFree(r.top,LTangle,RTangle);
        let resR = this.isFree(r.right,RTangle,RBangle);
        let resB = this.isFree(r.bot,RBangle,LBangle);
        
        if(set.nominalwidth < 150)
            resT = resB = true;
        if(set.nominalheight < 150)
            resL = resR = true;

        if(r.left==='groove' || r.top==='groove' || r.right==='groove' || r.bot==='groove'){
            return(
                <span className="fons12 red">Петли несовместимы с пазами</span>
            )
        }
        if(resL && resT && resR && resB){
            return(
                <span className="fons12 red">Нет вариантов</span>
            )
        }

        return(
            <>
            <label className="flex-column">
                <span>Сторона</span>
                <select onChange={this.handleInputChange} name="side" defaultValue="none" value={set.loops.side}>
                    <option key="loopnone" value="none">Нет</option>
                    <option key="loopleft" value="left"  disabled={resL}>Слева{resL && ' (занято)'}</option>
                    <option key="looptop" value="top"   disabled={resT}>Сверху{resT && ' (занято)'}</option>
                    <option key="loopright" value="right" disabled={resR}>Справа{resR && ' (занято)'}</option>
                    <option key="loopbot" value="bot"   disabled={resB}>Снизу{resB && ' (занято)'}</option>
                </select>
            </label>
  
            {this.state.side !== 'none' &&
            <>
            <label className="flex-column">
            <span>Кол-во, шт</span>
            <input 
                className={this.state.error ? 'error' : ''}
                onChange={this.handleInputChange} 
                name="count" 
                type="number"
                min="2"
                max={countMax}
                step="1" 
                defaultValue="2" />
            </label>
            <label className="flex-center ml-a"><span className="fons12">От верхнего/нижнего края отступ отверстий {beforeMin}мм.</span></label>
            <label className="flex-column w100p">
            <span>До первой петли, мм</span>
            <input 
                onChange={this.handleInputChange} 
                name="before" 
                type="range"
                min={beforeMin}
                max={beforeMax}
                step="1" 
                defaultValue={beforeMin} />
            </label>
            <label className="flex-column w100p">
            <span>До последней петли, мм</span>
            <input 
                onChange={this.handleInputChange} 
                name="after" 
                type="range"
                min={afterMin}
                max={afterMax}
                step="1" 
                defaultValue={afterMax} />
            </label>
            </>
            }
            </>
        );
    }
  }

function Loops(props){
    let set = props.set;
    let m = props.m;
    let loops = set.loops;
    let indent = 25;
    let radius = 35/2;

    if(loops.side === undefined || loops.side === 'none') return('');
  
    let width = set.width;
    let height = set.height;
    let circles = [];

    if(loops.side === 'left'){
        circles.push(<path key="lpath" d={`M ${m+indent} ${m} v ${height}`} stroke="#72aee6" strokeDasharray="10" fill="transparent"/>);
        circles.push(<Circle key={'lbefore'} side={loops.side} cx={m+indent} cy={+loops.before+m} r={radius} m={m} />);
        circles.push(<Circle key={'lafter'} side={loops.side} cx={m+indent} cy={+loops.after+m} r={radius} m={m} />);
    }
    else if(loops.side === 'top'){
        circles.push(<path key="tpath" d={`M ${m} ${m+indent} h ${width}`} stroke="#72aee6" strokeDasharray="10" fill="transparent"/>);
        circles.push(<Circle key={'tbefore'} side={loops.side} cx={+loops.before+m} cy={m+indent} r={radius} m={m} />);
        circles.push(<Circle key={'tafter'} side={loops.side} cx={+loops.after+m} cy={m+indent} r={radius} m={m} />);
    }
    else if(loops.side === 'right'){
        circles.push(<path key="rpath" d={`M ${m+width-indent} ${m} v ${height}`} stroke="#72aee6" strokeDasharray="10" fill="transparent"/>);
        circles.push(<Circle key={'rbefore'} side={loops.side} cx={m+width-indent} cy={+loops.before+m} r={radius} m={m} />);
        circles.push(<Circle key={'rafter'} side={loops.side} cx={m+width-indent} cy={+loops.after+m} r={radius} m={m} />);
    }
    else if(loops.side === 'bot'){
        circles.push(<path key="bpath" d={`M ${m} ${height+m-indent} h ${width}`} stroke="#72aee6" strokeDasharray="10" fill="transparent"/>);
        circles.push(<Circle key={'bbefore'} side={loops.side} cx={+loops.before+m} cy={height+m-indent} r={radius} m={m} />);
        circles.push(<Circle key={'bafter'} side={loops.side} cx={+loops.after+m} cy={height+m-indent} r={radius} m={m} />);
    }
        

    if(loops.count > 2){
        let steps = loops.count - 1;
        let step = Math.round((loops.after-loops.before)/steps, 1);
        for(let i = 1; i < steps; i++){
            let circleCX = m+indent;
            let circleCY = +loops.before+(step*i)+m;
            if(loops.side === 'top'){
                circleCY = m+indent;
                circleCX = +loops.before+(step*i)+m;
            }
            else if(loops.side === 'right'){
                circleCX = width+m-indent;
                circleCY = +loops.before+(step*i)+m;
            }
            else if(loops.side === 'bot'){
                circleCY = height+m-indent;
                circleCX = +loops.before+(step*i)+m;
            }
            circles.push(    
                <Circle 
                    key={`${circleCX}-${circleCY}-${i}`} 
                    cx={circleCX} 
                    cy={circleCY} 
                    r={radius} 
                    width={width} 
                    height={height} 
                    side={loops.side}
                    m={m} />);
        }
    }  
    return circles;
}
  
function Circle(props){
    let side = props.side;
    let line = `M ${props.cx} ${props.cy} h ${props.r + 20}`;
    let numberX = props.cx + 45;
    let numberY = props.cy + 8;
    let number = props.cy-props.m;
    if(side === 'top'){
        line = `M ${props.cx} ${props.cy} v ${props.r + 20}`;
        numberX = props.cx - 23;
        numberY = props.cy + 60;
        number = props.cx - props.m;
    }
    else if(side === 'right'){
        line = `M ${props.cx} ${props.cy} h -${props.r + 20}`;
        numberX = props.cx - 90;
        numberY = props.cy + 10;
        number = props.cy-props.m;
    }
    else if(side === 'bot'){
        line = `M ${props.cx} ${props.cy} v -${props.r + 20}`;
        numberX = props.cx - 20;
        numberY = props.cy - 50;
        number = props.cx - props.m;
    }
    return (
      <>
      <circle cx={props.cx} cy={props.cy} r={props.r} stroke="#3b8ecc" fill="#3b8ecc" />
      <path d={line}  stroke="#3b8ecc" />
      <text fontSize="150%" x={numberX} y={numberY}>{number}</text>
      </>
    );
}

export {LoopsOptions, Loops};