import React from 'react';
import './App.css';
import {LoopsOptions, Loops} from './components/loops';
import {GrooveOptions, Groove} from './components/groove';
import {EdgeOptions, Edge} from './components/edge';
import {Summary} from './components/summary';

class CopyDetail{
  constructor(data){
    this.detail = {
      'title': data.title + ' (копия)',
      'width': data.width,
      'nominalwidth': data.width,
      'minWidth': 60,
      'maxWidth': window.importOptions.SHEET_WIDTH,
      'height': data.height,
      'nominalheight': data.height,
      'minHeight': 60,
      'maxHeight': window.importOptions.SHEET_LENGHT,
      'count': 1,
      'reserve':{
        'left': data.reserve.left,
        'top': data.reserve.top,
        'right': data.reserve.right,
        'bot': data.reserve.bot
      },
      'lt': {title:'Левый верхний' , prev:'lb', next:'rt', x:data.lt.x, y:data.lt.y, type:data.lt.type},
      'rt': {title:'Правый верхний', prev:'lt', next:'rb', x:data.rt.x, y:data.rt.y, type:data.rt.type},
      'rb': {title:'Правый нижний' , prev:'rt', next:'lb', x:data.rb.x, y:data.rb.y, type:data.rb.type},
      'lb': {title:'Левый нижний'  , prev:'rb', next:'lt', x:data.lb.x, y:data.lb.y, type:data.lb.type},
      'loops': data.loops,
      'groove': data.groove,
      'grooveIndent': 16,
      'grooveWidth': 4,
      'grooveDeep': 8,
      'edge': data.edge,
      'edgeMin': 0.4,
      'edgeMax': 2.0,
      'textureRotate': 0,
    }
  }
  create(){
    return this.detail;
  }
}

class App extends React.Component {

  state = {
    details: new Map(),
    importOptions: window.importOptions,
    order: {
      billing: {
        userName: null,
        userEmail: null,
        userPhone: null,
        userComment: null,
      },
      faster: null,
      packaging: null,
    },
    activeDetail: null,
  }

  addNewDetailFunc = (e) => {
    let newDetail = {
      'title': "Деталь #" + (this.state.details.size + 1),
      'width': 400,
      'nominalwidth': 400,
      'minWidth': 60,
      'maxWidth': 2040,
      'height': 600,
      'nominalheight': 600,
      'minHeight': 60,
      'maxHeight': 2770,
      'count': 1,
      'reserve':{
        'left': false,
        'top': false,
        'right': false,
        'bot': false    
      },
      'lt': {title:'Левый верхний' , prev:'lb', next:'rt', x:0, y:0, type:'outside'},
      'rt': {title:'Правый верхний', prev:'lt', next:'rb', x:0, y:0, type:'outside'},
      'rb': {title:'Правый нижний' , prev:'rt', next:'lb', x:0, y:0, type:'outside'},
      'lb': {title:'Левый нижний'  , prev:'rb', next:'lt', x:0, y:0, type:'outside'},
      'loops': false,
      'groove': false,
      'grooveIndent': 16,
      'grooveWidth': 4,
      'grooveDeep': 8,
      'edge':{
        'left': 0.0,
        'top': 0.0,
        'right': 0.0,
        'bot': 0.0    
      },
      'edgeMin': 0.4,
      'edgeMax': 2.0,
      'textureRotate': 0,
    }
    let details = this.state.details;
    let detailKey = 'D-' + new Date().getTime();
    let activeDetail = detailKey;
    details.set(detailKey, newDetail);
    this.setState({details,activeDetail});

    return detailKey;
  }

  inputOnChange = (angleTitle, obj, detailKey) => {
    let details = this.state.details;
    let detail = details.get(detailKey);

    for(let key in obj){
      detail[angleTitle][key] = obj[key];
    }

    details.set(detailKey,detail);
    this.setState({details});
  }

  clearReserve = (set,item) => {
    for(let side in set.reserve){
      if(set.reserve[side] === item)
        set.reserve[side] = false;
    }
    if(item === 'loops')
      set.loops.side = false;
    
    return set;
  }

  handleSetDetailSize = (detailKey, side, value) => {
    let details = this.state.details;
    let detail = details.get(detailKey);

    detail[side] = value;      
    
    if(side === 'width' && value > 500){
      detail[side] = 500;
    }
    else if(side === 'width' && value < 150){
      detail[side] = 150;
    }
    else if(side === 'height' && value > 700){
      detail[side] = 700;
    }
    else if(side === 'height' && value < 150){
      detail[side] = 150;
    }

    // Сбрасываем петли, если для них нет места
    if(side === 'width' && value < 150){
      if(detail.loops){
        if(detail.loops.side === 'top' || detail.loops.side === 'bot'){
          detail = this.clearReserve(detail,'loops');
        }
      }
    }
    if(side === 'height' && value < 150){
      if(detail.loops){
        if(detail.loops.side === 'left' || detail.loops.side === 'right'){
          detail = this.clearReserve(detail,'loops');
        }
      }
    }
    
    detail['nominal'+side] = value;
    details.set(detailKey,detail);
    this.setState({details});
  }

  loopChange = (loops, detailKey) => {
    let details = this.state.details;
    let detail = details.get(detailKey);
    let width = detail.width;
    let height = detail.height;

    // Резервируем сторону детали, если она свободна
    if(!detail.reserve[loops.side]){
      for(let loopSide in detail.reserve){
        if(detail.reserve[loopSide] === 'loops')
          detail.reserve[loopSide] = false;
      }
      detail.reserve[loops.side] = 'loops';
    }
    
    // Здесь пересчитываем расположение последней петли, 
    // если оно дальше границы детали
    if(loops.side === 'left' || loops.side === 'right'){
      if(loops.after > height){
        loops.after = height - 100;
      }
    }
    else{
      if(loops.after > width){
        loops.after = width - 100;
      }
    }
    // Перезаписываем объект детали с новыми данными петель
    detail.loops = loops;
    details.set(detailKey,detail);
    this.setState({details});
  }

  grooveChange = (side, detailKey) => {
    let details = this.state.details;
    let detail = details.get(detailKey);

    // Резервируем сторону детали, если она свободна
    if(!detail.reserve[side]){
      for(let grooveSide in detail.reserve){
        if(detail.reserve[grooveSide] === 'groove')
          detail.reserve[grooveSide] = false;
      }
      if(side !== 'none'){
        detail.reserve[side] = 'groove';
        detail.groove = side;
      }
      else{
        detail.groove = false;
      }
    }

    details.set(detailKey,detail);
    this.setState({details});
  }

  edgeChange = (edge,detailKey) => {
    let details = this.state.details;
    let detail = details.get(detailKey);
    detail.edge = edge;
    details.set(detailKey,detail);
    this.setState({details});
  }

  changeDetailOptions = (e) => {
    let target = e.target;
    let activeDetail = target.closest('.detail').dataset.key;
    let action = target.dataset.action;
    let details = this.state.details;
    if(action === 'remove'){
      let confirm = window.confirm('Удалить деталь? Это действие нельзя отменить.');
      if(confirm){
        details.delete(activeDetail);
        this.setState({details});
      }
      return;
    }
    if(action === 'copy'){
      let d = new CopyDetail(details.get(activeDetail));
      let newDetail = d.create();
      let detailKey = 'D-' + new Date().getTime();
      activeDetail = detailKey;
      details.set(detailKey, newDetail);
      this.setState({details,activeDetail}, this.forceUpdate());
    }
    if(action === 'close'){
      this.setState({activeDetail: null});
      return;
    }
    if(action === 'rotate'){
      let detail = details.get(this.state.activeDetail);
      detail.textureRotate = detail.textureRotate > 0 ? 0 : 90;
      details.set(activeDetail,detail);
      this.setState({details});
      return;
    }
    this.setState({activeDetail});
  }
  
  render(){
     return (
      <>
      <Summary set={this.state.details} options={this.state.importOptions} />
      {this.state.details.size > 2 && <FormControls addNewDetailFunc={this.addNewDetailFunc} />}
      <DetailsList
        activeDetail={this.state.activeDetail} 
        changeDetailOptions={this.changeDetailOptions}
        list={this.state.details}
        loopF={this.loopChange} 
        grooveF={this.grooveChange} 
        edgeF={this.edgeChange}
        f={this.inputOnChange} 
        ff={this.handleSetDetailSize} />
      <FormControls addNewDetailFunc={this.addNewDetailFunc} />
      <div className="copyright">
        Приложение разработано в интернет-агентстве <a rel="noreferrer" target="_blank" href="https://alpha-max.ru">Альфамакс</a>
      </div>
      </>
    );
  }
}
export default App;

class DetailsList extends React.Component{
  state = {}

  render(){
    let list = [];
    let set = this.props.f;
    let setSizeF = this.props.ff;
    let loopF = this.props.loopF;
    let grooveF = this.props.grooveF;
    let edgeF = this.props.edgeF;
    let activeKey = this.props.activeDetail;
    let changeDetailOptions = this.props.changeDetailOptions;
    this.props.list.forEach(function(value,key) {
      list.push( <Detail 
        activeKey={activeKey === key ? 1 : 0}
        changeDetailOptions={changeDetailOptions}
        ff={setSizeF} 
        f={set} 
        loopF={loopF} 
        grooveF={grooveF}
        edgeF={edgeF} 
        key={key} 
        dkey={key} 
        value={value} /> );
    });
    return(
      <>
      <h2>Список деталей</h2>
      {list.length > 0 ? list : 'Список пуст'}
      </>
    );
  }
}

class Detail extends React.Component{
  state = {}

  handleSetSize = (e) => {
    let fieldName = e.target.name;
    let min = e.target.min;
    let max = e.target.max;
    let value = +e.target.value;
    let key = this.props.dkey;

    if(value < min || value > max)
      e.target.classList.add('error')
      if(value > max){
        value = max;
        e.target.classList.remove('error')
      }
    else
      e.target.classList.remove('error')

    this.props.ff(key,fieldName,value);
  }
  handleSetTitle = (e) => {
    let fieldName = e.target.name;
    let value = e.target.value;
    let key = this.props.dkey;

    this.props.ff(key,fieldName,value);
  }

  render(){
    let set = this.props.value;
    let o = window.importOptions;
    if(this.props.activeKey === 0){
      return(
        <div className="detail not-active" data-key={this.props.dkey}>
          <div className="detail-settings w100p">
            <div className="flex-nowrap">
              <fieldset id="detail-size" className="flex">
                <legend>Размеры</legend>
                <label className="w100p">
                  <input onChange={this.handleSetTitle} type="text" name="title" placeholder="Комментарий" value={set.title} />
                </label>
                <label className="flex-column">
                  <span>Ширина, мм</span>
                  <input onChange={this.handleSetSize} type="number" name="width" min="60" max={o.SHEET_WIDTH} value={set.nominalwidth} />
                </label>
                <label className="flex-column">
                  <span>Длина, мм</span>
                  <input onChange={this.handleSetSize} type="number" name="height" min="60" max={o.SHEET_LENGHT} value={set.nominalheight} />
                </label>
                <label className="flex-column">
                  <span>Кол-во, шт</span>
                  <input onChange={this.handleSetSize} type="number" name="count" min="1" max="1000" value={set.count} />
                </label>
              </fieldset>
              <fieldset className="detail-ext-options">
                <legend>Опции</legend>
                <button data-action="change" onClick={this.props.changeDetailOptions} className="btn">Редактировать</button>
                <button data-action="copy" onClick={this.props.changeDetailOptions} className="btn">Копировать</button>
              </fieldset>
            </div>
          </div>
          <div className="detail-plan">
            <DetailPlan set={this.props.value} />
            <button className="remove-detail-btn" data-action="remove" onClick={this.props.changeDetailOptions} title="Удалить деталь"></button>
          </div>
        </div>
      )
    }
    return(
      <div className="detail active" data-key={this.props.dkey}>
        <div className="detail-settings w100p">
          <div className="flex-nowrap">
            <fieldset id="detail-size" className="flex">
              <legend>Размеры</legend>
              <label className="w100p">
                <input onChange={this.handleSetSize} type="text" name="title" placeholder="Комментарий" value={set.title} />
              </label>
              <label className="flex-column">
                <span>Ширина, мм</span>
                <input onChange={this.handleSetSize} type="number" name="width" min="60" max={o.SHEET_WIDTH} value={set.nominalwidth} />
              </label>
              <label className="flex-column">
                <span>Длина, мм</span>
                <input onChange={this.handleSetSize} type="number" name="height" min="60" max={o.SHEET_LENGHT} value={set.nominalheight} />
              </label>
              <label className="flex-column">
                <span>Кол-во, шт</span>
                <input onChange={this.handleSetSize} type="number" name="count"  min="1" max="1000" value={set.count} />
              </label>
            </fieldset>

            <fieldset id="edge" className="flex">
              <legend>Кромка</legend>
              <EdgeOptions func={this.props.edgeF} set={this.props.value} />
            </fieldset>
            
            <div className="detail-ext-options">
              <button data-action="close" onClick={this.props.changeDetailOptions} className="btn">Свернуть</button>
              <button data-action="copy" onClick={this.props.changeDetailOptions} className="btn">Копировать</button>
              <button data-action="rotate" onClick={this.props.changeDetailOptions} className="btn">Повернуть текстуру</button>
            </div>
          </div>

          <fieldset id="angles" className="w100p">
            <legend>Углы</legend>
            <AngleOptions angleName='lt' func={this.props.f} set={this.props.value} />
            <AngleOptions angleName='rt' func={this.props.f} set={this.props.value} />
            <AngleOptions angleName='rb' func={this.props.f} set={this.props.value} />
            <AngleOptions angleName='lb' func={this.props.f} set={this.props.value} />
          </fieldset>

          <fieldset id="loops" className={set.loops && set.loops.side !== 'none' ? 'flex w100p' : 'flex w50p'}>
            <legend>Петли</legend>
            <LoopsOptions func={this.props.loopF} set={this.props.value} />
          </fieldset>

          <fieldset id="groove" className="flex w50p">
            <legend>Паз</legend>
            <GrooveOptions func={this.props.grooveF} set={this.props.value} />
          </fieldset>

        </div>
        <div className="detail-plan">
          <DetailPlan set={this.props.value} />
          <button className="remove-detail-btn" data-action="remove" onClick={this.props.changeDetailOptions} title="Удалить деталь"></button>
        </div>
      </div>
    )
  }
}

class AngleOptions extends React.Component{
  state = {
    active: false,
    type: 'outside',
    send: true,
    x: 30,
    y: 30,
  }

  handleInputChange = (event) => {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : Math.abs(target.value);
    const name = target.name;

    if(target.type === 'range' && this.state.send){
      const breakPoint = target.dataset.break;
      this.setState({
        x: value <= breakPoint ? value : breakPoint,
        y: value,
      })
    }
    else{
      this.setState({
        [name]: value
      });
    }
    
    this.props.func(
      this.props.angleName,
      { // Отправляем данные угла назад
        x:this.state.x,
        y:this.state.y,
        type:this.state.type,
        title:this.props.set[this.props.angleName].title,
        next:this.props.set[this.props.angleName].next,
        prev:this.props.set[this.props.angleName].prev
      },
      target.closest('.detail').dataset.key
    );
  }

  handleTypeChange = (e) => {
    let type = e.target.name;
    this.setState(function(state, props){
      this.props.func( // Отправляем данные угла назад
        props.angleName,
        {
          x:state.x,
          y:state.y,
          type:type,
          title:props.set[props.angleName].title,
          next:props.set[props.angleName].next,
          prev:props.set[props.angleName].prev,
        },
        e.target.closest('.detail').dataset.key
      );
      return {type: type};
    });
  }

  handleInputActive = (e) => {
    const value = e.target.checked;
    this.setState({
      active: value
    })
    this.props.func(
      this.props.angleName,
      { // Отправляем данные угла назад
        x: value ? this.state.x : 0,
        y: value ? this.state.y : 0,
        type:this.state.type,
        title:this.props.set[this.props.angleName].title,
        next:this.props.set[this.props.angleName].next,
        prev:this.props.set[this.props.angleName].prev
      },
      e.target.closest('.detail').dataset.key
    );
  }

  render(){
    let set = this.props.set;
    let next = set[set[this.props.angleName].next];
    let prev = set[set[this.props.angleName].prev];
    let maxX = 101;
    let maxY = 101;
    
    if(this.props.angleName === 'lt'){
      maxY = +set.width - next.y;
      maxX = +set.height - prev.x;
    }
    else if(this.props.angleName === 'rt'){
      maxY = +set.width - prev.y;
      maxX = +set.height - next.x;
    }
    else if(this.props.angleName === 'rb'){
      maxY = +set.width - next.y;
      maxX = +set.height - prev.x;
    }
    else{
      maxY = +set.width - prev.y;
      maxX = +set.height - next.x;
    }

    let input = <span><input onChange={this.handleInputActive} type="checkbox" name="active" checked={this.state.active} /></span>;
    // Если сторона зарезервирована
    let closedAngle = [];
    let reserveL = set.reserve.left;
    let reserveT = set.reserve.top;
    let reserveR = set.reserve.right;
    let reserveB = set.reserve.bot;
    let motive = '';
    if( reserveL === 'loops' || reserveL === 'groove'){
      closedAngle = ['lb','lt'];
      reserveL === "loops" ? motive = 'петлями' : motive = 'пазом';
    }
    else if(reserveT === 'loops' || reserveT === 'groove'){
      closedAngle = ['lt','rt'];
      reserveT === "loops" ? motive = 'петлями' : motive = 'пазом';
    }
    else if(reserveR === 'loops' || reserveR === 'groove'){
      closedAngle = ['rt','rb'];
      reserveR === "loops" ? motive = 'петлями' : motive = 'пазом';
    }
    else if(reserveB === 'loops' || reserveB === 'groove'){
      closedAngle = ['rb','lb'];
      reserveB === "loops" ? motive = 'петлями' : motive = 'пазом';
    }

    closedAngle.forEach((el)=>{
      if(this.props.angleName === el)
        input = <span className="reserve-notice red">Сторона занята {motive}</span>;
    })

    // Закрытое положение
    if(!this.state.active){
      return(
        <div className="angle-options-wrapper">
          <div className="angle-options-type w100p">
            <label className="angle-title-wrapper">
              <span className={`angle-title ${this.props.angleName}`}>{set[this.props.angleName].title}</span>
              {input}
            </label>
          </div>
        </div>
      )
    }
    // Открытое положение
    return(
      <div className={`angle-options-wrapper ${this.state.active && 'active'}`}>
        <div className="angle-options-type w100p">
          <label className="angle-title-wrapper">
            <span className={`angle-title ${this.props.angleName}`}>{set[this.props.angleName].title}</span>
            <span><input onChange={this.handleInputActive} type="checkbox" name="active" checked={this.state.active} /></span>
          </label>
          <button onClick={this.handleTypeChange} name="outside" className="angle-type__outside ml-a" data-active={this.state.type === 'outside' && 'active' }>Наружный</button>
          <button onClick={this.handleTypeChange} name="linear" className="angle-type__linear" data-active={this.state.type === 'linear' && 'active' }>Прямой</button>
          <button onClick={this.handleTypeChange} name="inside" className="angle-type__inside" data-active={this.state.type === 'inside' && 'active' }>Внутренний</button>
        </div>
        <label className="w100p pt10">
          X <input onChange={this.handleInputChange} type="range" name="y" min="30" max={maxY} data-break={maxX} step="1" value={this.state.y} />
            <span><b>{this.state.y}</b>&nbsp;мм</span>
        </label>
        <label className="switch">
          <input onChange={this.handleInputChange} name="send" type="checkbox" checked={this.state.send} />
        </label>
        <label className="w100p">
          Y <input onChange={this.handleInputChange} type="range" name="x" min="30" max={maxX} step="1" value={this.state.x} disabled={this.state.send} />
            <span><b>{this.state.x}</b>&nbsp;мм</span>
        </label>
      </div>
    );
  }
}

function correctSize(side,min,max){
  let m = Math.abs(side);
  if(m < min)
    return min;
  else if(m > max)
    return max;
  else
    return m;
}

function DetailPlan(props){
  let set = props.set;
  // Раздаём значения
  let {width,nominalwidth,minWidth,maxWidth,height,nominalheight,minHeight,maxHeight,lt,rt,lb,rb} = set;
  let m = 70;

  width = correctSize(width,minWidth,maxWidth);
  height = correctSize(height,minHeight,maxHeight);

  // Пробуем сохранить пропорции
  if(nominalwidth >= 500)
    height = set.height = Math.round(500/((Math.round(nominalwidth/nominalheight*10,-1)/10)));
  if(height < 200)
    height = set.height = 200;
  
  let ltA   = `M ${m} ${(Math.abs(lt.x) + m)} Q ${angleControlDotLT(m,lt)} ${(Math.abs(lt.y) + m)} ${m}`;
  let top   = ` L ${(width-rt.y + m)} ${m}`;
  let rtA   = ` Q ${angleControlDotRT(m,rt,set)} ${width+m} ${+rt.x + m}`;
  let right = ` L ${(width + m)} ${(height - rb.x + m)}`;
  let rbA   = ` Q ${angleControlDotRB(m,rb,set)} ${(width - rb.y + m)} ${(height + m)}`;
  let bot   = ` L ${(+lb.y+m)} ${(height + m)}`;
  let lbA   = ` Q ${angleControlDotLB(m,lb,set)} ${m} ${height-lb.x+m}`;

  let bgWidth = 200*3;
  let bgHeight = 133*3;
  let bgVector = set.textureRotate;

  return(
    <svg xmlns="http://www.w3.org/2000/svg" version="1.1" viewBox={"0 0 " + (width+m*2) +' '+ (height+m*2)}>
      {/* ФОН */}
      <defs>
        <pattern 
          id="bg" 
          patternUnits="userSpaceOnUse" 
          preserveAspectRatio="none" 
          patternTransform={`rotate(${bgVector}, ${width/2}, ${height/2})`}
          width={bgWidth}
          height={bgHeight}>
          <image 
            href={'http://rondo.96min.ru' + window.importOptions.SHEET_PICTURE_URL} 
            x={0} y={0} 
            width={bgWidth} 
            height={bgHeight} />
        </pattern>
      </defs>
      {/* Деталь */}
      <path d={ltA + top + rtA + right + rbA + bot + lbA + ' z'} stroke="#333333" strokeWidth="3" strokeLinejoin="miter" fill="url(#bg)" opacity="0.7"/>
      {/* Размеры углов */}
      <path d={`M ${m} ${Math.abs(lt.x) + m} h ${lt.y} v -${lt.x}`}        stroke="red" strokeDasharray="10" fill="transparent"/>
      <path d={`M ${width+m} ${Math.abs(rt.x) + m} h -${rt.y} v -${rt.x}`} stroke="red" strokeDasharray="10" fill="transparent"/>
      <path d={`M ${width+m} ${height - rb.x + m} h -${rb.y} v ${rb.x}`}   stroke="red" strokeDasharray="10" fill="transparent"/>
      <path d={`M ${m} ${height - lb.x + m} h ${+lb.y} v ${+lb.x}`}        stroke="red" strokeDasharray="10" fill="transparent"/>
      {/* Размер детали */}
      <text fontSize="150%" x={(m+width)/2} y={20}>{nominalwidth}мм</text>
      <text fontSize="150%" transform={`rotate(-90,${20},${(m+height)/2})`} x={m/2-50} y={(m+height)/2}>{nominalheight}мм</text>
      {/* Петли */} <Loops set={set} m={m} />
      {/* Паз */} <Groove set={set} m={m} />
      {/* Кромка */} <Edge set={set} m={m} />
    </svg>
  )
}

function angleControlDotLT(m,angle){
  if(angle.type === 'outside')
    return `${m} ${m}`;
  else if(angle.type === 'linear')
    return `${angle.x/2 + m} ${angle.y/2 + m}`;
  else if(angle.type === 'inside')
    return `${+angle.x + m} ${+angle.y + m}`;
}
function angleControlDotRT(m,angle,set){
  if(angle.type === 'outside')
    return `${(+set.width + m)} ${m}`;
  else if(angle.type === 'linear')
    return `${+set.width - angle.y/2 + m} ${angle.x/2 + m}`;
  else if(angle.type === 'inside')
    return `${+set.width - angle.y + m} ${+angle.x + m}`;
}
function angleControlDotRB(m,angle,set){
  if(angle.type === 'outside')
    return `${(+set.width + m)} ${+set.height + m}`;
  else if(angle.type === 'linear')
    return `${+set.width - angle.y/2 + m} ${+set.height - angle.x/2 + m}`;
  else if(angle.type === 'inside')
    return `${+set.width - angle.y + m} ${+set.height - angle.x + m}`;
}
function angleControlDotLB(m,angle,set){
  if(angle.type === 'outside')
    return `${m} ${+set.height + m}`;
  else if(angle.type === 'linear')
    return `${+angle.y/2 + m} ${+set.height - angle.x/2 + m}`;
  else if(angle.type === 'inside')
    return `${+angle.y + m} ${+set.height - angle.x + m}`;
}

function FormControls(props){
  return(
    <div id="calc-form-controlls-wrapper">
      <button className="btn" onClick={props.addNewDetailFunc} >Добавить деталь</button>
      <button className="btn" disabled>Перейти к оформлению</button>
    </div>
  );
}